.loading {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 9;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8); }

.loading .loading__bg {
  position: relative;
  width: 100%;
  height: 100%;
  background-size: auto 100%;
  background-position: top left;
  background-repeat: no-repeat; }

.loading .loading__progress {
  position: absolute;
  left: 2.3%;
  bottom: 0.3rem;
  width: 95.3%;
  height: 0.24rem; }

.loading .loading__animate {
  position: relative;
  width: 14%;
  height: 100%;
  background: -webkit-gradient(linear, left top, right top, from(#00c6ff), to(#7a35bb));
  background: linear-gradient(to right, #00c6ff 0%, #7a35bb 100%);
  border-radius: 0.12rem;
  -webkit-transition: width 300ms ease-out;
  transition: width 300ms ease-out; }

.loading .loading__stars {
  position: absolute;
  top: -0.1rem;
  left: -0.08rem;
  z-index: 1;
  width: 0.36rem;
  height: 0.45rem; }

.loading .loading__star--item {
  position: absolute;
  width: 0.24rem;
  height: 0.26rem;
  line-height: 1; }

.loading .loading__star--img {
  display: block;
  width: 100%;
  height: auto; }

.loading .loading__star--1,
.loading .loading__star--2 {
  -webkit-transform: scale(0.8);
  transform: scale(0.8); }

.loading .loading__star--1 {
  top: -0.04rem;
  right: -0.06rem;
  -webkit-animation: loading__star--animate 1s linear infinite;
  animation: loading__star--animate 1s linear infinite; }

.loading .loading__star--2 {
  top: 0;
  left: -0.02rem;
  -webkit-animation: loading__star--animate 1.5s 0.25s linear infinite;
  animation: loading__star--animate 1.5s 0.25s linear infinite; }

.loading .loading__star--3 {
  bottom: 0.02rem;
  right: 0;
  -webkit-animation: loading__star3--animate 2s linear infinite;
  animation: loading__star3--animate 2s linear infinite; }

.loading .loading__animate--widget {
  position: absolute;
  right: 0;
  top: -0.41rem;
  width: 1.06rem;
  height: 0.82rem; }

.loading .loading__animate--num {
  position: absolute;
  top: 0.41rem;
  left: -0.44rem;
  z-index: 1;
  width: 0.5rem;
  height: 0.24rem;
  line-height: 0.24rem;
  color: #fff;
  font-size: 0.18rem;
  font-weight: bold;
  text-align: right; }

.loading .loading__animate--fish {
  overflow: hidden;
  position: absolute;
  top: 0;
  right: -0.2rem;
  z-index: 3;
  width: 1.26rem;
  height: 0.82rem;
  line-height: 1; }

.loading .loading__fish--1,
.loading .loading__fish--2 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: auto; }

.loading .loading__fish--1 {
  -webkit-animation: loading__fish--animate 1s linear infinite;
  animation: loading__fish--animate 1s linear infinite; }

.loading .loading__fish--2 {
  -webkit-animation: loading__fish--animate 1s 0.5s linear infinite;
  animation: loading__fish--animate 1s 0.5s linear infinite; }

.loading .loading__animate--star {
  position: absolute;
  left: 0.42rem;
  top: 0.3rem;
  z-index: 2;
  width: 0.24rem; }

.loading .loading__animate--item {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  line-height: 1;
  list-style: none; }

.loading .loading__animate--item1 {
  -webkit-transition: opacity 0.2s linear 0.8s, left 0.8s linear, top 0.8s ease-in, -webkit-transform 0.8s linear;
  transition: opacity 0.2s linear 0.8s, left 0.8s linear, top 0.8s ease-in, -webkit-transform 0.8s linear;
  transition: opacity 0.2s linear 0.8s, transform 0.8s linear, left 0.8s linear, top 0.8s ease-in;
  transition: opacity 0.2s linear 0.8s, transform 0.8s linear, left 0.8s linear, top 0.8s ease-in, -webkit-transform 0.8s linear; }

.loading .loading__animate--item2 {
  -webkit-transition: opacity 0.2s linear 0.9s, left 0.9s linear, top 0.9s ease-in, -webkit-transform 0.9s linear;
  transition: opacity 0.2s linear 0.9s, left 0.9s linear, top 0.9s ease-in, -webkit-transform 0.9s linear;
  transition: opacity 0.2s linear 0.9s, transform 0.9s linear, left 0.9s linear, top 0.9s ease-in;
  transition: opacity 0.2s linear 0.9s, transform 0.9s linear, left 0.9s linear, top 0.9s ease-in, -webkit-transform 0.9s linear; }

.loading .loading__animate--item3 {
  -webkit-transition: opacity 0.2s linear 1s, left 1s linear, top 1s ease-in, -webkit-transform 1s linear;
  transition: opacity 0.2s linear 1s, left 1s linear, top 1s ease-in, -webkit-transform 1s linear;
  transition: opacity 0.2s linear 1s, transform 1s linear, left 1s linear, top 1s ease-in;
  transition: opacity 0.2s linear 1s, transform 1s linear, left 1s linear, top 1s ease-in, -webkit-transform 1s linear; }

.loading__fadeOut {
  -webkit-animation: loading__fadeOut--animate 300ms ease-out;
  animation: loading__fadeOut--animate 300ms ease-out; }

@-webkit-keyframes loading__fadeOut--animate {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

@keyframes loading__fadeOut--animate {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

@-webkit-keyframes loading__star--animate {
  0% {
    -webkit-transform: scale(0.8) translateY(0);
    transform: scale(0.8) translateY(0); }
  25% {
    -webkit-transform: scale(0.8) translateY(0.04rem);
    transform: scale(0.8) translateY(0.04rem); }
  50% {
    -webkit-transform: scale(0.8) translateY(0);
    transform: scale(0.8) translateY(0); }
  75% {
    -webkit-transform: scale(0.8) translateY(-0.04rem);
    transform: scale(0.8) translateY(-0.04rem); }
  100% {
    -webkit-transform: scale(0.8) translateY(0);
    transform: scale(0.8) translateY(0); } }

@keyframes loading__star--animate {
  0% {
    -webkit-transform: scale(0.8) translateY(0);
    transform: scale(0.8) translateY(0); }
  25% {
    -webkit-transform: scale(0.8) translateY(0.04rem);
    transform: scale(0.8) translateY(0.04rem); }
  50% {
    -webkit-transform: scale(0.8) translateY(0);
    transform: scale(0.8) translateY(0); }
  75% {
    -webkit-transform: scale(0.8) translateY(-0.04rem);
    transform: scale(0.8) translateY(-0.04rem); }
  100% {
    -webkit-transform: scale(0.8) translateY(0);
    transform: scale(0.8) translateY(0); } }

@-webkit-keyframes loading__star3--animate {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0); }
  25% {
    -webkit-transform: translateY(0.04rem);
    transform: translateY(0.04rem); }
  50% {
    -webkit-transform: translateY(0);
    transform: translateY(0); }
  75% {
    -webkit-transform: translateY(-0.04rem);
    transform: translateY(-0.04rem); }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0); } }

@keyframes loading__star3--animate {
  0% {
    -webkit-transform: translateY(0);
    transform: translateY(0); }
  25% {
    -webkit-transform: translateY(0.04rem);
    transform: translateY(0.04rem); }
  50% {
    -webkit-transform: translateY(0);
    transform: translateY(0); }
  75% {
    -webkit-transform: translateY(-0.04rem);
    transform: translateY(-0.04rem); }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0); } }

@-webkit-keyframes loading__fish--animate {
  0%,
  100% {
    height: auto; }
  50% {
    height: 0; } }

@keyframes loading__fish--animate {
  0%,
  100% {
    height: auto; }
  50% {
    height: 0; } }
